import React, {Component} from 'react';
import {map} from "lodash";
import {connect} from "react-redux";
import {uModuleRequest} from "../../../store/actions/uModule";
import {withRouter} from "react-router-dom";


class HeaderSearch extends Component {
    constructor(props) {
        super(props);
        this.state = {
            body: {},
            fold: false
        }
    }


    handleFold(){
        this.setState(prevState => {
            return {
                fold: !prevState.fold
            }
        })
    }

    handleSearch(){
        this.props.readTableInformationCallback(this.state.body)
    }
    handleClear(){
        this.setState({body: {}})
    }
    handleSearchInput(e){
        this.setState(prevState => {return {body: {...prevState.body,[e.target.name]:e.target.value}}})
    }
    handleSearchInputCheckBox(e){
        this.setState(prevState => {return {body:{...prevState.body, [e.target.name]: e.target.checked}}})
    }

    render() {
        const query = (trip) =>{

            switch (trip[0]){
                case 'UPDATE': return false;
                case 'DELETE': return false;
                case 'date': return false;
                case 'index': return false;

                default:{
                    switch (trip[2]){
                        case 'checkbox': {return <input checked={this.state.body[trip[0]]} name={trip[0]} className={"col-sm-6 input-text"} type={trip[2]} onChange={(e)=>this.handleSearchInputCheckBox(e)}></input>}
                        default: return <input  value={this.state.body[trip[0]] || this.props.prevState[trip[0]]} name={trip[0]} className={"col-sm-6 input-text"} type={trip[2]} onChange={(e)=>this.handleSearchInput(e)}></input>
                    }

                }
            }
        }
        return (
            <div className={"row  justify-content-start  pb-5 " }>
                    <div className="row justify-content-end align-items-center">
                        <div className="row col justify-content-end align-items-center">
                            <i onClick={(e)=>this.handleFold(e)} className="col-1 bi bi-arrow-bar-down fs-2"></i>
                        </div>
                    </div>
                    {this.state.fold ?
                        <div className={"swing-in-top-fwd" }>
                                <div className="row col align-content-between ">
                                        {map(this.props.header, (triplet)=>{
                                            if(!query(triplet)){return <div/>}
                                            return (
                                                <div className="row align-items-center bg-dark border rounded p-1 mt-3 ">
                                                    <div className="col-6 text-truncate">
                                                        {triplet[1]}
                                                    </div>
                                                    <div className="row col-6 justify-content-center fs-5">
                                                        {query(triplet)}
                                                    </div>
                                                </div>
                                            )

                                        })}
                                    </div>
                                <div className="row justify-content-center align-items-center  p-3">
                                    <div className="row align-items-center justify-content-evenly col mb-3 ">
                                        <button className="btn btn-danger fw-bold fs-6 col-4" onClick={(e) => this.handleSearch(e)}>Buscar</button>
                                        <button className="btn btn-danger fw-bold fs-6 col-4" onClick={(e) => this.handleClear(e)}>Limpiar</button>
                                    </div>
                                </div>
                        </div> : <div/>
                    }
            </div>
        );
    }
}

export default connect(
    state => ({
        table: state.UMODULE,
    }),
    dispatch => ({
        readTableInformation:( payload)=> dispatch(uModuleRequest(payload))
    })
)(withRouter(HeaderSearch))
